	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapitre 3 - La fuite","texte":{"childMarkdownRemark":{"html":"<p>L’homme sourit et recula pour sortir de la pièce avec d’autres scientifiques. Me laissant en compagnie de la dizaine de gardes quelque peu stressés par ma prestation de tout à l’heure. J’essaie de m’asseoir, mais deux gardes se mettent à me crier de rester coucher, je vois que certains avaient mis la main sur leur arme à feu, prêts à tirer dans le pire des cas. Je peux bouger les mains, je commence à sentir la texture du drap ainsi que ma peau, si on peut dire, je savais que ce n’était pas ma peau à moi, mais elle l’était maintenant. Je pousse un long soupir et ferme les yeux.</p>\n<p>Garde 1 : Tu penses qu’il a des armes dans son bras?<br>\nGarde 2 : Monsieur Darakei a dit que oui, mais il va les activer plus tard, répondit sur le même ton un autre garde à proximité.</p>\n<p>Je me dis que ça pourrait être amusant de voir si c’est activable par moi-même. Je me concentre et sens des systèmes s’activer dans mon bras. J’entends des cris des gardes à ma gauche qui crient et reculent, réclamant que je range ça. J’ouvre les yeux et lève un peu la tête pour voir un pistolet qui est sorti de mon avant-bras. Je souris et le range, au grand soupir des gardes.</p>\n<p>Wamien : C’est quoi vos noms les gars?<br>\nGarde 1 : Tais-toi, tu n’as pas besoin de parler.</p>\n<p>Je soupire et me tais. Je pense que je pourrais tous les tuer et foutre le camp d’ici. Autant passer la parole avant les actes.</p>\n<p>Wamien : Vous savez quand je pourrais sortir d’ici? Pas parce que vous êtes de mauvaise compagnie hein?<br>\nGarde 2 : On t’a dit de te taire et non tu ne sortiras pas d’ici!<br>\nWamien : Alors je sortirai d’ici par moi-même.</p>\n<p>Je brise d’un coup les barres du lit et les sangles qui me gardaient en place.</p>\n<p>Les gardes s’écrièrent en prenant en mains leurs armes. Je fais sortir deux pistolets de mes bras pour commencer à leur tirer en pleine tête les uns après les autres. Les gardes me tirant à quelques reprises dessus, ne sentant pas les balles des armes à feu. Je saute sur deux gardes qui entrent dans la pièce pour leur cogner la tête et sortir de la pièce qui me gardait prisonnière. Je vois trois gardes sur chacun de mes côtés qui se mettent en position de tir. Je souris et ferme les yeux, me concentre sur les armes des hommes et les démonte en petites pièces.</p>\n<p>Wamien : Dégagez de mon chemin les gars, si vous tenez à la vie.</p>\n<p>Deux gardes à ma gauche courent vers moi. Je me place en position, ayant en moi les connaissances de plusieurs arts martiaux. Le premier garde arrive à proximité, prêt à donner un coup avec son poing gauche. Je lui attrape le poignet, exerce un léger pivot sur mes pieds, le corps du garde s’allonge sur mon dos et je l’envoie vers les trois gardes qui avaient commencé à avancer vers moi. Le garde-missile heurte un des gardes et tous deux restent au sol. 2-0 pour moi. Le deuxième garde me cogne en arrière de la tête. Je bascule un peu vers l’avant et m’agrippe à sa chemise et réussis à lui donner un coup au visage pour ensuite le lâcher et le voir finir dans le mur. Deux gardes arrivent à proximité de moi, donnant lieu à des échanges de coups entre nous, tous les trois évitant la plupart des coups. Je finis par en mettre un K.O. et le deuxième ne perd pas de temps à suivre son coéquipier. Les deux gardes qui étaient encore présents deux minutes avant sont partis. Sans doute pour aller avertir Monsieur Darakei. Une alarme commence à sonner dans tout le complexe.</p>\n<p>Étrangement, il n’y avait plus personne dans les couloirs, je réussis à trouver la porte de sortie. Je m’avance vers la porte et approche ma main de la barre métallique. J’entends un petit « fssshhh » comme une flèche ou un tranquillisant qui est tiré. Dans la fraction de seconde qui suit, je sens une puissante décharge électrique parcourir tout mon corps. Je tombe dans les pommes.</p>"}},"chapitreAvant":"chapitre-2-les-modifications","chapitreApres":"chapitre-4-l-entrainement","nomRoman":"le-premier-cyborg-tome-1","slug":"chapitre-3-la-fuite","codeChapitre":"H0001C003","equivalentUrl":"/stories/chapter/chapter-3-the-escape"}},"pathContext":{"slug":"chapitre-3-la-fuite"}}

